
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #333;
}
*{margin: 0;padding: 0
}
a{    
  color: #333;
  text-decoration: none;
}
.log{
  padding: 50px 0;
  background-color: #f6bb41;
}
.log img{
  width: 100px;
}
.menu{
  padding: 30px 0;
  background: url(../../static/img/bg1.1e438f8.png);
}
.menu ul{
  list-style: none;
}
.menu ul li{
  display: inline-block;
  margin-right: 30px;
}
.menu ul li .little{
  font-size: 10px;
}
.container{
  background: url(../../static/img/bg2.d74bff1.png);
}
.footer{
    position: relative;
    max-width: 700px;
    margin: auto;
}
.footer .top{
    text-align: left;
    padding: 20px 30px;
    background-color: white;
}
.footer .top .f1{
    width: 120px;
}
.footer .top .f2{
    width: 60px;
    position: absolute;
    top: 9px;
    right: 30px;
}
.footer .top p{
    display: block;
    margin-top: 15px;
    font-size: 12px;
}
.footer .top p:last-child{
    display: block;
    margin-top: 0;
    margin-bottom: 5px;
}
.footer .bottom{
  padding: 15px 50px;
  color: white;
  background-color: #313131;
}
.footer .bottom:after{
  content: "";
  display: block;
  clear: both;
}
.footer .bottom .left{
      width: 132px;
          height: 20px;
    text-align: left;
  white-space: nowrap;
  float: left;
  position: relative;
}
.footer .bottom .left img{
  width: 26px;
}
.footer .bottom .left span{
  font-size: 13px;
  position: absolute;
  top: 2px;
    left: 30px;
}
.footer .bottom .right{
  float: right;
      font-size: 12px;
    margin-left: 20px;
}

